
@charset "UTF-8";
.show-enter-active, .show-leave-active {
  transition: opacity .3s;
  position: fixed;
  -webkit-transition: opacity .3s;
}
.show-enter, .show-leave-to {
  position: fixed;
  -moz-opacity: 0;
  /* 老版Mozilla */
  -khtml-opacity: 0;
  /* 老版Safari */
  opacity: 0;
  /* 支持opacity的浏览器*/
}
.dby-logView-content {
  color: #fff;
  background: #fff;
  position: relative;
  margin: 0 auto;
}
.logger-view-body {
  position: fixed;
  z-index: 1000;
  width: 0;
  height: 0;
}
.dby-logView-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 80%;
  max-width: 33.6rem;
  min-width: 17.06667rem;
  height: auto;
  z-index: 2000;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  background: #fff;
  border-radius: 0.16rem;
  overflow: hidden;
}
.dby-logView-content-h3 {
  margin: 0;
  padding: 0.53333rem;
  text-align: center;
  font-size: 1.49333rem;
  font-weight: 800;
  opacity: 0.8;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 0.08rem 0.08rem 0 0;
  color: #fff;
}
.scroll-div-height {
  height: 24rem;
}
.dby-logView-botton-block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.dby-logView-botton {
  width: 100%;
  line-height: 2.13333rem;
  color: #000;
  text-align: center;
  font-size: 1.06667rem;
  height: 2.13333rem;
  font-weight: 600;
  background: #f0f9eb;
}
.dby-logView-logList {
  height: 21.33333rem;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  padding: 0.4rem 1.06667rem 0.8rem;
  margin: 0;
  font-weight: 300;
  font-size: 1.15rem;
  color: #000;
}
.dby-logView-info, .dby-logView-log {
  color: #000;
}
.dby-logView-warn {
  color: #E6A23C;
}
.dby-logView-error {
  color: #F56C6C;
}
.dby-logView-content-span {
  font-size: 0.85333rem;
  padding-bottom: 0.4rem;
  line-height: 1.06667rem;
  display: block;
  white-space: normal;
  word-break: break-all;
  border-top: 1px solid #ebeef5;
  border-left: 1px solid #ebeef5;
  border-right: 1px solid #ebeef5;
  font-family: "微软雅黑";
}
.dby-logView-content-block {
  border-bottom: 1px solid #ebeef5;
}
.dby-logView-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  background: #000;
  -moz-opacity: 0.5;
  /* 老版Mozilla */
  -khtml-opacity: 0.5;
  /* 老版Safari */
  opacity: 0.5;
  /* 支持opacity的浏览器*/
}
.dby-logView-bug-svg {
  width: 0.8rem;
  height: 0.8rem;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.dby-logView-bug-button {
  position: fixed;
  z-index: 1000;
  top: 0.13333rem;
  right: 0.13333rem;
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: 0;
  margin: 0;
  color: #fff;
  background-color: #f56c6c;
  border-color: #f56c6c;
  font-size: 0.64rem;
  border-radius: 0.16rem;
  padding: 0.32rem;
}



